$label-color: rgba(28, 1, 255, 1);
@mixin clearfix() {
  &::after {
    display: block;
    content: "";
    clear: both;
  }
}

.loader{
    background-color: #eeeeee7a;
    position: absolute;
    width: calc(100% - 20px);
    height: 100vh;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 12;
}
